"use client";

import { useState } from "react";
import { updateNews } from "@/services/news.service";

interface Props {
  news: any;
  onSuccess: () => void;
  onCancel: () => void;
}

export default function EditNewsForm({
  news,
  onSuccess,
  onCancel,
}: Props) {
  const [title, setTitle] = useState(news.title);
  const [excerpt, setExcerpt] = useState(news.excerpt || "");
  const [content, setContent] = useState(news.content);
  const [type, setType] = useState(news.type);
  const [status, setStatus] = useState(news.status);
  const [image, setImage] = useState<File | null>(null);
  const [loading, setLoading] = useState(false);

  const handleSubmit = async (e: any) => {
    e.preventDefault();
    setLoading(true);

    try {
      const formData = new FormData();
      formData.append("title", title);
      formData.append("excerpt", excerpt);
      formData.append("content", content);
      formData.append("type", type);
      formData.append("status", status);
      if (image) formData.append("image", image);

      await updateNews(news.uuid, formData);

      onSuccess();
    } catch (error) {
      console.error("Update news error", error);
    } finally {
      setLoading(false);
    }
  };

  return (
    <form
      onSubmit={handleSubmit}
      className="bg-gray-900 p-6 rounded-lg shadow-lg space-y-4"
    >
      <h3 className="text-xl font-bold text-white">ÿ™ÿπÿØŸäŸÑ ÿßŸÑÿÆÿ®ÿ±</h3>

      <input
        type="text"
        value={title}
        onChange={(e) => setTitle(e.target.value)}
        className="w-full p-3 rounded bg-gray-800 text-white"
      />

      <textarea
        value={excerpt}
        onChange={(e) => setExcerpt(e.target.value)}
        className="w-full p-3 rounded bg-gray-800 text-white"
        rows={2}
      />

      <textarea
        value={content}
        onChange={(e) => setContent(e.target.value)}
        className="w-full p-3 rounded bg-gray-800 text-white"
        rows={6}
      />

      <div className="flex gap-4">
        <select
          value={type}
          onChange={(e) => setType(e.target.value)}
          className="p-2 rounded bg-gray-800 text-white"
        >
          <option value="latest">ÿÆÿ®ÿ± ÿπÿßÿØŸä</option>
          <option value="breaking">ÿÆÿ®ÿ± ÿπÿßÿ¨ŸÑ</option>
        </select>

        <select
          value={status}
          onChange={(e) => setStatus(e.target.value)}
          className="p-2 rounded bg-gray-800 text-white"
        >
          <option value="draft">ŸÖÿ≥ŸàÿØÿ©</option>
          <option value="published">ŸÖŸÜÿ¥Ÿàÿ±</option>
        </select>
      </div>

      <input
        type="file"
        onChange={(e) => setImage(e.target.files?.[0] || null)}
        className="text-white"
      />

      <div className="flex gap-3">
        <button
          type="submit"
          disabled={loading}
          className="bg-yellow-500 hover:bg-yellow-600 px-6 py-2 rounded text-white"
        >
          {loading ? "ÿ¨ÿßÿ±Ÿç ÿßŸÑÿ™ÿ≠ÿØŸäÿ´..." : "ÿ™ÿ≠ÿØŸäÿ´"}
        </button>

        <button
          type="button"
          onClick={onCancel}
          className="bg-gray-600 px-6 py-2 rounded text-white"
        >
          ÿ•ŸÑÿ∫ÿßÿ°
        </button>
      </div>
    </form>
  );
}
