"use client";

import { useState } from "react";
import { createNews } from "@/services/news.service";

interface Props {
  onSuccess: () => void;
}

export default function CreateNewsForm({ onSuccess }: Props) {
  const [title, setTitle] = useState("");
  const [excerpt, setExcerpt] = useState("");
  const [content, setContent] = useState("");
  const [type, setType] = useState("latest");
  const [status, setStatus] = useState("draft");
  const [image, setImage] = useState<File | null>(null);
  const [loading, setLoading] = useState(false);

  const handleSubmit = async (e: any) => {
    e.preventDefault();
    setLoading(true);

    try {
      const formData = new FormData();
      formData.append("title", title);
      formData.append("excerpt", excerpt);
      formData.append("content", content);
      formData.append("type", type);
      formData.append("status", status);
      if (image) formData.append("image", image);

      await createNews(formData);

      // ÿ™ŸÜÿ∏ŸäŸÅ ÿßŸÑŸÅŸàÿ±ŸÖ
      setTitle("");
      setExcerpt("");
      setContent("");
      setType("latest");
      setStatus("draft");
      setImage(null);

      onSuccess();
    } catch (error) {
      console.error("Create news error", error);
    } finally {
      setLoading(false);
    }
  };

  return (
    <form
      onSubmit={handleSubmit}
      className="bg-gray-900 p-6 rounded-lg shadow-lg space-y-4"
    >
      <h3 className="text-xl font-bold text-white">ÿ•ÿ∂ÿßŸÅÿ© ÿÆÿ®ÿ± ÿ¨ÿØŸäÿØ</h3>

      <input
        type="text"
        placeholder="ÿπŸÜŸàÿßŸÜ ÿßŸÑÿÆÿ®ÿ±"
        value={title}
        onChange={(e) => setTitle(e.target.value)}
        className="w-full p-3 rounded bg-gray-800 text-white"
        required
      />

      <textarea
        placeholder="ŸàÿµŸÅ ŸÖÿÆÿ™ÿµÿ±"
        value={excerpt}
        onChange={(e) => setExcerpt(e.target.value)}
        className="w-full p-3 rounded bg-gray-800 text-white"
        rows={2}
      />

      <textarea
        placeholder="ŸÖÿ≠ÿ™ŸàŸâ ÿßŸÑÿÆÿ®ÿ±"
        value={content}
        onChange={(e) => setContent(e.target.value)}
        className="w-full p-3 rounded bg-gray-800 text-white"
        rows={6}
        required
      />

      <div className="flex gap-4">
        <select
          value={type}
          onChange={(e) => setType(e.target.value)}
          className="p-2 rounded bg-gray-800 text-white"
        >
          <option value="latest">ÿÆÿ®ÿ± ÿπÿßÿØŸä</option>
          <option value="breaking">ÿÆÿ®ÿ± ÿπÿßÿ¨ŸÑ</option>
        </select>

        <select
          value={status}
          onChange={(e) => setStatus(e.target.value)}
          className="p-2 rounded bg-gray-800 text-white"
        >
          <option value="draft">ŸÖÿ≥ŸàÿØÿ©</option>
          <option value="published">ŸÖŸÜÿ¥Ÿàÿ±</option>
        </select>
      </div>

      <input
        type="file"
        onChange={(e) => setImage(e.target.files?.[0] || null)}
        className="text-white"
      />

      <button
        type="submit"
        disabled={loading}
        className="bg-green-600 hover:bg-green-700 px-6 py-2 rounded text-white"
      >
        {loading ? "ÿ¨ÿßÿ±Ÿç ÿßŸÑÿ≠ŸÅÿ∏..." : "ÿ≠ŸÅÿ∏"}
      </button>
    </form>
  );
}
